import React from 'react';
import Input from '@iso/components/uielements/input';
import Checkbox from '@iso/components/uielements/checkbox';
import Button from '@iso/components/uielements/button';
import { notification } from '@iso/components';
import IntlMessages from '@iso/components/utility/intlMessages';
import {
    signInWithEmail,
    signUpWithEmailAndPassword,
    resetPassword,
} from '@iso/lib/firebase/firebase.authentication.util';


export const MySignIn = props => {

    const [state, setState] = React.useState({
        visible: false,
        email: '',
        password: '',
        confirmLoading: false,
    });
    const handleChange = e => {
        const { name, value } = e.target;
        setState({
            ...state,
            [name]: value,
        });
    };

    const handleLogin = async () => {
        const { email, password } = state;
        if (!(email && password)) {
            notification('error', 'Please fill in your email and password');
            return;
        }
        setState({
            ...state,
            confirmLoading: true,
        });
        let user;
        let message;
        if (props.signup) {
            try {
                await signUpWithEmailAndPassword(email, password).then(authUser => {
                    user = authUser.user;
                    console.log(user, 'User');
                });
            } catch (error) {
                message = error.message;
                console.log(error.message, 'Error');
            }
        } else {
            try {
                await signInWithEmail(email, password).then(authUser => {
                    user = authUser.user;
                });
            } catch (error) {
                message = error.message;
                console.log(error.message, 'Error');
            }
        }
        if (user) {
            const uid = user.uid;

            const token = await user.getIdToken();
            const data = {
                uid: uid,
                token: token
            }
            props.login(data);
            props.history.push('/dashboard');
        } else {
            notification('error', message);
            setState({
                ...state,
                confirmLoading: false,
            });
        }
    };
    const handleResetPassword = () => {
        const { email } = state;
        if (!email) {
            notification('error', `Please fill in email.`);
            return;
        }
        resetPassword(email)
            .then(() =>
                notification('success', `Password reset email sent to ${email}.`)
            )
            .catch(error => notification('error', 'Email address not found.'));
    };


    return (
        <form>
            <div className="isoInputWrapper">
                <label>Email</label>
                <Input
                    name="email"
                    size="large"
                    placeholder="Email"
                    value={state.email}
                    onChange={handleChange}
                />
            </div>

            <div className="isoInputWrapper">
                <label>Password</label>
                <Input
                    name="password"
                    type="password"
                    size="large"
                    placeholder="Password"
                    value={state.password}
                    onChange={handleChange}
                />
            </div>
            <span className="isoResetPass" onClick={handleResetPassword}>
                Reset Password
          </span>
            <div className="isoInputWrapper isoLeftRightComponent">
                <Checkbox>
                    <IntlMessages id="page.signInRememberMe" />
                </Checkbox>
                <Button type="primary" onClick={handleLogin}>
                    <IntlMessages id="page.signInButton" />
                </Button>
            </div>

            <p className="isoHelperText">
                <IntlMessages id="page.signInPreview" />
            </p>
        </form>
    )
}